/**
 * RISIVO CRM - Demo Login API
 * 
 * This endpoint handles demo user authentication by:
 * 1. Validating the demo access token
 * 2. Verifying the access code (if required)
 * 3. Creating a JWT session for the demo user
 */

import { NextResponse } from 'next/server'
import { cookies } from 'next/headers'
import { prisma } from '@/lib/db'
import jwt from 'jsonwebtoken'

export const runtime = 'nodejs'
export const dynamic = 'force-dynamic'

interface DemoLoginRequest {
  token: string
  accessCode?: string
}

export async function POST(request: Request) {
  console.log('[Demo Login] Request received')
  
  try {
    const body: DemoLoginRequest = await request.json()
    const { token, accessCode } = body

    if (!token) {
      return NextResponse.json(
        { success: false, error: 'Demo token is required' },
        { status: 400 }
      )
    }

    if (!prisma) {
      return NextResponse.json(
        { success: false, error: 'Database not available' },
        { status: 503 }
      )
    }

    // Find the demo access record
    const demoAccess = await prisma.demoAccess.findUnique({
      where: { accessToken: token },
    })

    if (!demoAccess) {
      return NextResponse.json(
        { success: false, error: 'Invalid demo access token' },
        { status: 404 }
      )
    }

    // Check if expired
    if (new Date() > demoAccess.expiresAt) {
      await prisma.demoAccess.update({
        where: { id: demoAccess.id },
        data: { status: 'expired' },
      })
      return NextResponse.json(
        { success: false, error: 'This demo access has expired' },
        { status: 410 }
      )
    }

    // Check if revoked
    if (demoAccess.status === 'revoked') {
      return NextResponse.json(
        { success: false, error: 'This demo access has been revoked' },
        { status: 403 }
      )
    }

    // Verify access code if required
    if (demoAccess.accessCode) {
      if (!accessCode) {
        return NextResponse.json(
          { success: false, error: 'Access code is required' },
          { status: 400 }
        )
      }
      if (accessCode !== demoAccess.accessCode) {
        return NextResponse.json(
          { success: false, error: 'Invalid access code' },
          { status: 401 }
        )
      }
    }

    // Update demo access status and usage
    await prisma.demoAccess.update({
      where: { id: demoAccess.id },
      data: {
        status: 'active',
        lastUsedAt: new Date(),
        usageCount: { increment: 1 },
      },
    })

    // Create JWT payload for demo user
    const jwtSecret = process.env.NEXTAUTH_SECRET || process.env.AUTH_SECRET
    if (!jwtSecret) {
      console.error('[Demo Login] No JWT secret configured')
      return NextResponse.json(
        { success: false, error: 'Server configuration error' },
        { status: 500 }
      )
    }

    const jwtPayload = {
      id: `demo-${demoAccess.id}`,
      email: demoAccess.email,
      name: demoAccess.name || 'Demo User',
      role: 'demo',
      isMasterAdmin: false,
      status: 'active',
      agencyId: null,
      agencyName: 'Demo Agency',
      sub: `demo-${demoAccess.id}`,
    }

    // Create the JWT token
    const jwtToken = jwt.sign(
      jwtPayload,
      jwtSecret,
      { expiresIn: '7d' }  // Demo sessions expire in 7 days
    )

    console.log('[Demo Login] Demo session created for:', demoAccess.email)

    // Create the response
    const response = NextResponse.json({
      success: true,
      user: {
        id: jwtPayload.id,
        email: jwtPayload.email,
        name: jwtPayload.name,
        role: jwtPayload.role,
        company: demoAccess.company,
        expiresAt: demoAccess.expiresAt.toISOString(),
      },
    })

    // Get cookies instance
    const cookieStore = await cookies()
    
    // Determine cookie name based on environment
    const isProduction = process.env.NODE_ENV === 'production'
    const cookieName = isProduction ? '__Secure-authjs.session-token' : 'authjs.session-token'
    
    // Set the session cookie
    cookieStore.set(cookieName, jwtToken, {
      httpOnly: true,
      secure: isProduction,
      sameSite: 'lax',
      path: '/',
      maxAge: 7 * 24 * 60 * 60, // 7 days
    })

    // Also set demo access token cookie for middleware to detect demo users
    cookieStore.set('demo_access_token', token, {
      httpOnly: true,
      secure: isProduction,
      sameSite: 'lax',
      path: '/',
      maxAge: 7 * 24 * 60 * 60, // 7 days
    })

    return response

  } catch (error) {
    console.error('[Demo Login] Error:', error)
    return NextResponse.json(
      { success: false, error: 'Failed to process demo login' },
      { status: 500 }
    )
  }
}
